import { BrowserModule } from '@angular/platform-browser';
import { NgModule } from '@angular/core';
import { RouterModule, Routes, Route } from '@angular/router';
import { FormsModule } from '@angular/forms';
import { HttpClientInMemoryWebApiModule } from 'angular-in-memory-web-api';
import { InMemoryDataService } from './in-memory-data.service';
import { HttpClientModule } from '@angular/common/http';



import { AppComponent } from './app.component';
import { LoginComponent } from './login/login.component';
import { RegistrationComponent } from './registration/registration.component';
import { ForgotPwdComponent } from './forgot-pwd/forgot-pwd.component';
import { CartsComponent } from './carts/carts.component';
import { CheckoutComponent } from './checkout/checkout.component';
import { ContactComponent } from './contact/contact.component';
import { HomeComponent } from './home/home.component';
import { ProductsComponent } from './products/products.component';
import { OrderComponent } from './order/order.component';
import { ItemDetailsComponent } from './item-details/item-details.component';
import { UserListComponent } from './user-list/user-list.component';
import { LoginService } from './login/login.service';
import { SharedService } from './shared.service';
import { Observable } from 'rxjs-compat';


const approute:Routes=[
  {path:'',redirectTo:'Login',pathMatch:'full'},
  {path:'Login',component:LoginComponent},
  {path:'Registration',component:RegistrationComponent},
  {path:'ForgotPwd',component:ForgotPwdComponent},
  {
    path:'Home',component:HomeComponent,
    children:[
      {path: '', component: ProductsComponent},
      {path:'Contact',component:ContactComponent},
      {path:'Orders',component:OrderComponent},
      {path:'UsersList',component:UserListComponent},
      {path:'Carts',component:CartsComponent},
      {path:'CheckOut',component:CheckoutComponent},
      {path:'Products',component:ProductsComponent},
      {path:'ItemDetails/:id',component:ItemDetailsComponent}
    ]
  }
];


@NgModule({
  declarations: [
    AppComponent,LoginComponent,RegistrationComponent,ForgotPwdComponent,CartsComponent,
    CheckoutComponent,ContactComponent,HomeComponent,ProductsComponent,OrderComponent,ItemDetailsComponent,
    UserListComponent],
  imports: [BrowserModule,FormsModule,RouterModule.forRoot(approute),HttpClientModule,
    HttpClientInMemoryWebApiModule.forRoot(InMemoryDataService)],
  providers: [LoginService, InMemoryDataService, SharedService],
  bootstrap: [AppComponent]
})
export class AppModule { }